.login {
  box-sizing: border-box;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  font-family: 'Bree Serif', serif; }
  .login__container {
    display: flex;
    flex-flow: row nowrap;
    width: 80%;
    height: 60%;
    margin-bottom: 1em; }
    .login__container__main-image {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-around;
      width: 30%; }
      .login__container__main-image__img {
        width: 30%;
        object-fit: contain; }
    .login__container__form {
      display: flex;
      flex-flow: column;
      justify-content: center;
      align-items: center;
      width: 50%;
      height: 100%; }
      .login__container__form__title {
        font-size: 1.5em; }
      .login__container__form div {
        width: 100%;
        margin-bottom: 1em; }
        .login__container__form div p {
          margin-bottom: .2em; }
        .login__container__form div input {
          -webkit-transition: all .3s ease;
          transition: all .3s ease;
          box-sizing: border-box;
          width: 100%;
