          outline: none;
          border: 0;
          border-radius: 10px;
          background-color: #F2F2F2;
          font-family: 'Bree Serif'; }
        .login__container__form div input:focus {
          -webkit-transition: all .3s ease;
          transition: all .3s ease;
          -webkit-transform: scale(1.02);
